.homepage {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}

.text {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.intro-text {
  box-sizing: border-box;
  left: 70px;
  display: flex;
  width: 50%;
  min-width: 100px;
  padding: 0 30px;
  position: relative;
  top: -50px;
  height: fit-content;
}

.intro-text h2 {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 1.5em;
  line-height: 1em;
  letter-spacing: 0.05em;
  position: relative;
}

.intro-text h1 {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  line-height: 1em;
  display: flex;
  letter-spacing: 0.05em;
  position: relative;
  top: 0px;
}

.intro-text p {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 1em;
  display: flex;
  letter-spacing: 0.05em;
  top: 200px;
  position: relative;
}

.big-name {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: flex-end;
  position: relative;
  top: 12px;
  right: 100px;
}

.big-name h1 {
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 10em;
  line-height: 1em;
  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 0;
}

.big-name p {
  position: relative;
  top: 80px;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 0.9em;
  line-height: 1em;
  display: flex;
  align-items: center;
  letter-spacing: 0.1em;
  justify-content: flex-end;
  color: #ffffff;
}

@media only screen and (max-width: 650px) {
  .big-name h1 {
    font-size: 8em;
  }
  .big-name p {
    color: black;
    font-size: 12px;
    padding: 10px;
  }
  .intro-text {
    box-sizing: border-box;
    padding: 20px;
  }
  .intro-text h2 {
    font-size: 1em;
  }
  .intro-text h1 {
    font-size: 1em;
  }
  .intro-text p {
    font-size: 1em;
  }
}

@media only screen and (max-width: 1200px) {
  .text {
    flex-direction: column;
  }

  .big-name {
    width: 100%;
    right: 0;
    align-items: center;
    top: -80px;
  }

  .big-name h1 {
    margin: 0 auto;
  }

  .intro-text {
    position: absolute;
    justify-content: space-around;
    align-items: center;
    top: 500px;
    width: 100%;
    left: 0;
  }

  .intro-text h1 {
    margin: 0;
    padding: 0;
    top: 0;
    font-size: 0px;
    font-size: 1em;
  }
  .intro-text h2 {
    margin: 0;
    padding: 0;
    font-size: 1em;
  }
  .intro-text p {
    margin: 0;
    padding: 0;
    top: 0;
    font-size: 1em;
  }
}
