.App {
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
}

button {
  border: none;
  background: #9493b6;
  color: white;
  padding: 10px;
  border-radius: 30px;
  transition: all 0.1s ease-in;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  outline: none;
}

button:hover {
  color: #9493b6;
  background: white;
  outline: none;
}

a {
  color: black;
}

a:hover {
  color: #9493b6;
}
