
.about{
    min-height:100vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    box-sizing: border-box;
}

h1{
    text-transform: uppercase;
    padding: 50px 0;
}

.cols{
    width:100%;
    display:flex;
    justify-content: space-evenly;
}

.cols > div{
    width: 200px;
    height: 60vh;
    background-color: #EEEEEE;
    padding: 0px 10px;
}

.cols > div > h2{
    text-align: center;
}

ul{
    list-style: none;
}

ul li::before {
    content: "\2022";
    color:  #6C63FF;
    font-weight: bold;
    display: inline-block; 
    width: 1em;
    margin-left: -1em;
    font-size: 1em;
  }



