.project__home {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  background-repeat: no-repeat;
}

.project__title {
  padding: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

.col__netflix img {
  width: 100px;
  object-fit: contain;
}

.col__facebook img {
  width: 40px;
}

ul {
  margin: 0;
}

.project__cols {
  display: flex;
}

.project__cols img {
  padding: 20px;
}

.project__cols > div {
  background-color: #eeeeee;
  margin: 0 20px;
  transition: transform 450ms;
  position: relative;
  padding: 0px 20px 50px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project__cols > div:hover {
  transform: scale(1.05);
}

.col__title {
  text-align: center;
}

.col__list {
  margin-bottom: 10px;
}

.col__links {
  text-align: center;
  align-items: center;
  width: 50%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 0;
  padding: 10px;
}

.col__buttons > button {
  margin-right: 10px;
}

@media only screen and (max-width: 1200px) {
  .project__title {
    padding-bottom: 0px;
  }

  .project__cols {
    flex-direction: column;
  }
  .project__cols > div {
    margin-bottom: 20px;
  }
}
