.project {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 100px;
}

.project__title {
  padding: 20px;
}

.project .cols {
  display: flex;
  background: white;
}

.project .left-col {
  width: 60%;
  background: white;
}

.project .right-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 40%;
  background: white;
  padding: 50px;
}

.project img {
  width: 100%;
}

.right-buttons {
  display: flex;
}
.right-buttons > * {
  margin-right: 20px;
}

.view__icon {
  margin-right: 10px;
  font-size: 24px;
}

.view-live,
.view-code {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 900px) {
  .project .cols {
    flex-direction: column;
    align-items: center;
  }
  .cols > div {
    height: 100%;
  }
}
