.dock{
    background: rgba(255, 255, 255, 0.36);
    width: 400px;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin:0 auto;
    border-radius: 20px;
    position:fixed;
    bottom:0;
    left: 0;
    right: 0;
}

.dock img{
    width: 68px;
}



